  font-family: "Young Style", sans-serif;
  font-family: "Young Style Underlines", sans-serif;
  src: url("https://fonts.cdnfonts.com/css/young-style") format("woff2");
}
#app {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}
#app .photos {
  max-width: 500px;
}
#app .product-img {
  max-width: 100%;
  height: auto;
}
#app #back-out {
  font-size: 20px;
  font-family: "Young Style", sans-serif;
  text-decoration: none;
  color: #bcaaa4;
  margin-bottom: -30px;
}
#app #item-name {
  font-size: 30px;
  margin-top: 40px;
  font-family: "Young Style", sans-serif;
  margin-bottom: -10px;
  color: #4a3f35;
}
#app #item {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 20px;
}
#app #item .photos {
  border: #4a3f35 solid 4px;
}
#app #item #item-info {
  text-align: center;
  margin-left: 50px;
  background-color: #bcaaa4;
  width: 300px;
  border-radius: 8px;
  padding: 10px;
  height: 400px;
  padding-top: 100px;
}
#app #item #item-info #item-price {
  font-size: 50px;
  margin-bottom: 5px;
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
}
#app #item #item-info #item-description {
  margin-top: 50px;
  font-size: 16px;
  color: #555;
  margin-left: 8px;
  margin-right: 8px;
}
#app #item #item-info #add-to-cart {
  margin-top: 100px;
  background-color: #bcaaa4;
  height: 80px;
  width: 140px;
  color: #fff7e6;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}
#app #item #item-info #add-to-cart:hover {
  background-color: #4a3f35;
